.questions {
  position: relative;
  min-height: 795px;
  margin-bottom: 89px;
  background-color: $color-gable-green;

  @include vp-1199 {
    min-height: 676px;
    margin-bottom: 89px;
  }

  @include vp-767 {
    min-height: 621px;
    margin-bottom: 64px;
  }
}

.questions__img {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 0;
}

.questions__container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 313px 231px 120px 231px;
  color: $color-default-white;
  z-index: 1;

  @include vp-1199 {
    padding: 197px 45px 120px 45px;
  }

  @include vp-767 {
    padding: 179px 15px 65px 15px;
  }
}

.questions__container h2 {
  width: 402px;
  padding-right: 17px;
  margin: 0 0 17px 0;
  text-align: center;
  font-size: 35px;
  font-weight: 800;
  line-height: 35px;

  @include vp-1199 {
    padding-right: 0;
    margin-bottom: 18px;
    width: 292px;
    font-size: 25px;
    font-weight: 800;
    line-height: 25px;
  }

  @include vp-767 {
    width: 242px;
    margin-bottom: 14px;
    font-size: 22px;
    font-weight: 800;
    line-height: 22px;
  }
}

.questions__container p {
  width: 603px;
  padding-right: 17px;
  margin: 0 0 40px 0;
  text-align: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;

  @include vp-1199 {
    padding-right: 0;
    margin-bottom: 29px;
    width: 312px;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
  }

  @include vp-767 {
    width: 290px;
    margin-bottom: 25px;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
  }
}
