.contacts-map {
  width: 48.75%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  min-height: 324px;
  margin: 0 auto;
}
@media (max-width: 1199.5px) {
  .contacts-map {
    min-height: 340px;
    width: 100%;
  }
}
@media (max-width: 767.5px) {
  .contacts-map {
    min-height: 320px;
  }
}

.contacts-map__static-map {
  position: relative;
  min-height: 324px;
  grid-column: 1/2;
  grid-row: 1/2;
}
@media (max-width: 1199.5px) {
  .contacts-map__static-map {
    width: 100%;
    height: 100%;
  }
}
@media (max-width: 767.5px) {
  .contacts-map__static-map {
    min-height: 320px;
  }
}

.contacts-map__static-map::after {
  position: absolute;
  content: " ";
  top: 59.9%;
  left: 58.9%;
  width: 48px;
  height: 48px;
  background-image: url("../img/sprite/map-pin.svg");
  background-repeat: no-repeat;
  background-size: 48px 48px;
}
@media (max-width: 1199.5px) {
  .contacts-map__static-map::after {
    width: 42px;
    height: 42px;
    background-size: 42px 42px;
    top: 53%;
    left: 57.9%;
  }
}
@media (max-width: 767.5px) {
  .contacts-map__static-map::after {
    width: 28px;
    height: 28px;
    background-size: 28px 28px;
    top: 56.6%;
    left: 69.6%;
  }
}

.contacts-map__static-map img {
  width: 100%;
  height: auto;
  min-height: 324px;
  object-fit: cover;
  display: block;
  margin: 18px auto 0 auto;
}
@media (max-width: 1199.5px) {
  .contacts-map__static-map img {
    min-height: 340px;
    height: auto;
    margin: 0;
  }
}
@media (max-width: 767.5px) {
  .contacts-map__static-map img {
    min-height: 320px;
  }
}

.contacts-map__interactive-map {
  position: relative;
  display: block;
  grid-column: 1/2;
  grid-row: 1/2;
  height: auto;
  width: 100%;
  min-height: 324px;
  margin: 18px auto 0 auto;
}
@media (max-width: 1199.5px) {
  .contacts-map__interactive-map {
    width: 100%;
    min-height: 340px;
    margin-top: 0;
  }
}
@media (max-width: 767.5px) {
  .contacts-map__interactive-map {
    min-height: 320px;
  }
}

.contact-map__map-pin {
  width: 48px;
  height: 48px;
}
@media (max-width: 1199.5px) {
  .contact-map__map-pin {
    width: 42px;
    height: 42px;
  }
}
@media (max-width: 767.5px) {
  .contact-map__map-pin {
    width: 28px;
    height: 28px;
  }
}

