.tour-slider {
  position: relative;
  width: 31.5%;
  height: auto;
  object-fit: cover;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: $color-gable-green;
  color: $color-default-white;
  aspect-ratio: 380/412;

  @include vp-1199 {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    min-width: 324px;
    width: 30%;
    object-fit: contain;
    background-size: cover;
    background-position: center 0;
    aspect-ratio: 324/412;
  }

  @include vp-767 {
    min-width: 290px;
    min-height: 412px;
    height: auto;
    padding-bottom: 8px;
    background-position: 0 center;
    background-size: 100% 100%;
    aspect-ratio: 290/370;
  }
}

.tour-slider::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.6) 31.9%, rgba(0, 0, 0, 0) 70%, rgba(0, 0, 0, 0) 100%);
  z-index: 3;
}

.tour-slider--background-1 {
  background-image: url("../img/tour-background-1.jpg");

  @include retina {
    background-image: url("../img/tour-background-1@2x.jpg");
  }

  @include vp-1199 {
    background-image: url("../img/tour-background-1-tablet.jpg");

    @include retina {
      background-image: url("../img/tour-background-1-tablet@2x.jpg");
    }
  }

  @include vp-767 {
    background-image: url("../img/tour-background-1-mobile.jpg");

    @include retina {
      background-image: url("../img/tour-background-1-mobile@2x.jpg");
    }
  }
}

.tour-slider--background-2 {
  background-image: url("../img/tour-background-2.jpg");

  @include retina {
    background-image: url("../img/tour-background-2@2x.jpg");
  }

  @include vp-1199 {
    background-image: url("../img/tour-background-2-tablet.jpg");

    @include retina {
      background-image: url("../img/tour-background-2-tablet@2x.jpg");
    }
  }

  @include vp-767 {
    background-image: url("../img/tour-background-2-mobile.jpg");

    @include retina {
      background-image: url("../img/tour-background-2-mobile@2x.jpg");
    }
  }
}

.tour-slider--background-3 {
  background-image: url("../img/tour-background-3.jpg");

  @include retina {
    background-image: url("../img/tour-background-3@2x.jpg");
  }

  @include vp-1199 {
    background-image: url("../img/tour-background-3-tablet.jpg");

    @include retina {
      background-image: url("../img/tour-background-3-tablet@2x.jpg");
    }
  }

  @include vp-767 {
    background-image: url("../img/tour-background-3-mobile.jpg");

    @include retina {
      background-image: url("../img/tour-background-3-mobile@2x.jpg");
    }
  }
}

.tour-slider__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100%;
}

.tour-slider__theme {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: start;
  padding: 205px 20px 0 20px;
  margin-bottom: 13px;
  color: $color-default-white;
  font-size: 12px;
  font-weight: 500;
  line-height: 12px;
  z-index: 4;

  @include vp-1199 {
    width: 324px;
    padding: 204.5px 20px 0 21px;
  }

  @include vp-767 {
    width: 220px;
    margin-bottom: 5px;
    padding: 197px 20px 0 20px;
    flex-wrap: wrap;
    row-gap: 3px;
  }
}

.tour-slider__place svg {
  margin-right: 6px;
}

.tour-slider__days svg,
.tour-slider__nights svg {
  margin-right: 15px;
}

.tour-slider__theme span {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

.tour-slider__place {
  width: 31.5%;
  background-color: $color-navy-blue;
  padding: 8px;

  @include vp-1199 {
    width: 34.1%;
    padding-left: 0;
    padding-top: 9px;
    padding-bottom: 9px;
  }

  @include vp-767 {
    order: 3;
    min-width: 97px;
  }
}

.tour-slider__days {
  width: 42%;

  @include vp-1199 {
    width: 35.1%;
  }

  @include vp-767 {
    width: 70px;
    margin-right: 20px;
  }
}

.tour-slider__nights {
  width: 26.5%;

  @include vp-1199 {
    width: 30.8%;
  }

  @include vp-767 {
    width: 80px;
  }
}

.tour-slider--second .tour-slider__place {
  width: 32%;
  padding-right: 14px;
}

.tour-slider--second .tour-slider__days {
  width: 44%;

  @include vp-1199 {
    width: 40%;
  }
}

.tour-slider--second .tour-slider__nights {
  width: 24%;

  @include vp-1199 {
    width: 28%;
  }
}

.tour-slider--second .tour-slider__days svg {
  margin-right: 7px;
}

.tour-slider--second .tour-slider__nights svg {
  margin-right: 8px;
}

.tour-slider h3 {
  position: relative;
  width: 100%;
  padding: 0 20px;
  margin: 0 0 38px 0;
  font-size: 24px;
  font-weight: 700;
  line-height: 28px;
  z-index: 4;

  @include vp-1199 {
    width: 285px;
  }

  @include vp-767 {
    width: 250px;
    margin-bottom: 30px;
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;
  }
}

.tour-slider__options {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  padding: 0 21px 0 20px;
  margin-bottom: 10px;
  z-index: 4;

  @include vp-1199 {
    width: 313px;
  }

  @include vp-767 {
    margin-bottom: 5px;
  }
}

.tour-slider__option {
  margin-bottom: 10px;
}

.tour-slider__option p {
  margin: 0 0 6px 0;
  font-weight: 300;
  line-height: 100%;
}

.tour-slider__option p:nth-child(1n) {
  font-size: 12px;
}

.tour-slider__option p:nth-child(2n) {
  font-size: 13px;


  @include vp-767 {
    font-size: 12px;
    font-weight: 700;
  }
}

.tour-slider__option--second p:nth-child(1n),
.tour-slider__option--third p:nth-child(1n) {
  margin-bottom: 8px;

  @include vp-767 {
    margin-bottom: 6px;
  }
}

.tour-slider__option--first {
  width: 39%;

  @include vp-767 {
    width: 88px;
  }
}

.tour-slider__option--second {
  width: 40%;

  @include vp-767 {
    width: 99px;
  }
}

.tour-slider__option--third {
  width: 20%;
}

.tour-slider__option--second p:nth-child(2n),
.tour-slider__option--third p:nth-child(2n) {
  letter-spacing: 0.5px;

  @include vp-767 {
    letter-spacing: 0;
  }
}
